import React from 'react';

class App extends React.Component {

  constructor(props){
    super(props);
    this.state = {
      items: [],
      isLoaded: false,
    }
  }

  componentDidMount(){
    fetch('https://jsonplaceholder.typicode.com/users')
      .then(res => res.json())
      .then(json => {
        this.setState({
          isLoaded: true,
          items: json,
        })
      });
  }

  render(){ 

    var{ isLoaded, items} = this.state;

    if(!isLoaded){
      return <div>Loading</div>
    }
    else{

      return (
        <div className="App">
            <table border="1">
                  <th>Name</th>
                  <th>Email</th>
              {items.map(item =>(
                  <tr>
                  <td key={item.id}>
                      {item.name}
                  </td>
                  <td key={item.id}>
                      {item.email}
                  </td>
                </tr>
              ))}
            
            </table>
        </div>
      );
    }
}}

export default App;
